@import url(https://fonts.googleapis.com/css?family=Red+Hat+Mono:wght@100..900&display=swap);
.gs-grid{--cellSize: 2.2em;--brColor: #0003;display:grid;grid-gap:2em;gap:2em}.gs-grid__field{--columns: 16;--rows: 7;display:grid;grid-template-columns:repeat(var(--columns), 1fr);grid-template-rows:repeat(var(--rows), 1fr);grid-auto-flow:column;border:1px solid var(--brColor);border-width:0 0 1px 1px}.gs-grid__cursor{background-color:rgba(255,196,0,.178);grid-column:calc(var(--cursor) + 1);grid-row:1/span var(--rows);width:var(--cellSize);pointer-events:none}.gs-key{--button-face: #444f;--button-gradient: radial-gradient(hsla(0, 0%, 100%, 0.02), #fff0);background-color:var(--button-face);border:1px solid var(--brColor);border-width:1px 1px 0 0;border-radius:2px;background-image:var(--button-gradient);box-sizing:border-box;display:grid;grid-column:calc(var(--step) + 1);grid-row:calc(var(--rows) - var(--note));grid-template-areas:"main";height:var(--cellSize);place-items:center;transition:background-color 100ms,color 100ms;-webkit-user-select:none;-ms-user-select:none;user-select:none;width:var(--cellSize)}.gs-key:before{content:"";grid-area:main;padding-top:100%}.gs-key>*{grid-area:main}.gs-key--has-hilight{--button-face: rgb(99, 89, 36);transition:none}.gs-key--is-active{--button-face: rgb(255, 196, 0);--button-gradient: radial-gradient(hsl(59, 100%, 50%), rgba(255, 0, 0, 0));z-index:1;box-shadow:0 0 30px 5px rgba(255,166,0,.205);color:#000c;transition:background-color 800ms,color 100ms}.gs-key--is-active.gs-key--has-hilight{--button-face: rgb(255, 248, 147);transition:none}.gs-key__label{display:none;font-family:"Red Hat Mono";font-weight:100;font-size:.625em}
html,body{font-family:"Red Hat Mono";margin:0;padding:0}html{color:#fff8;height:100vh;background-color:#333}body{display:grid;place-items:center;height:100%}a,a:visited{color:#b90}
